
#cover{
    position: fixed;
    top:0;
    left:0;
    width: 100%;
    height:100vh;
    /* background: rgba(0, 0, 0, 0.75); */
    z-index: 9;
    display: none;
    opacity:0;
    /* pointer-events: none; */
    transition: opacity 0.5s ease-out;
}
#cover.covered{
    /* pointer-events: none; */
    display: block;
    
}

#my-div {
    position: absolute;
    z-index: 200;
    background-color: #f1f1f1;
    text-align: center;
    border: 1px solid #d3d3d3;
    top:25%;
    left: 25%;
    

}
#my-div-header {
    padding: 10px;
    cursor: move;
    z-index: 10;
    background-color: #2196F3;
    color: #fff;
}

.sizeBar {
    float: right
}

.bigBoi {
    width: 50%;
    height: 50%
}
.hidden{
    display: none;
}
.hideMe{
    float: left;
    cursor: pointer;
}
